/**
 * Mock API - Unified Interface for All Mocking Capabilities
 * 
 * This module provides the main `mock` namespace with all mocking functionality
 * including function mocks, spies, timers, and module mocking.
 */

import { fn, resetGlobalCallOrder } from './mockFunction.js';
import { spyOn, restoreAllSpies, restoreAllSpiesGlobally } from './spyOn.js';
import { 
  mockModule, 
  unmockModule, 
  clearAllMocks as clearAllModuleMocks,
  isMocked,
  getMockedModules,
  getMockImplementation 
} from './stubsRegistry.js';
import * as timers from '../timers/fakeTimers.js';

/**
 * Main mock API object - provides Jest/Sinon-compatible interface
 */
export const mock = {
  // Mock functions
  fn,
  
  // Spies
  spyOn,
  restoreAllSpies,
  
  // Module mocking
  module: mockModule,
  unmock: unmockModule,
  isMocked,
  getMockedModules,
  getMockImplementation,
  
  // Timers
  timers: {
    useFakeTimers: timers.useFakeTimers,
    useRealTimers: timers.useRealTimers,
    setSystemTime: timers.setSystemTime,
    advanceTimersByTime: timers.advanceTimersByTime,
    runAllTimers: timers.runAllTimers,
    runOnlyPendingTimers: timers.runOnlyPendingTimers,
    getTimerCount: timers.getTimerCount,
    now: timers.now
  },
  
  // Cleanup functions
  clearAllMocks() {
    clearAllModuleMocks();
    restoreAllSpiesGlobally();
    resetGlobalCallOrder();
  },
  
  resetAllMocks() {
    this.clearAllMocks();
    timers.useRealTimers();
  }
};

// Individual exports for convenience
export { fn } from './mockFunction.js';
export { spyOn } from './spyOn.js';
export { 
  useFakeTimers,
  useRealTimers, 
  setSystemTime,
  advanceTimersByTime,
  runAllTimers,
  runOnlyPendingTimers
} from '../timers/fakeTimers.js';

// Default export
export default mock;